.modal-film__backdrop {
  width: 100vw;
  height: 100vh;
  opacity: 1;
  visibility: visible;
  transition: opacity var(--main-effect), visibility var(--main-effect);
  background-color: #00000040;
  justify-content: center;
  padding: 20px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.modal-film__modal {
  width: 280px;
  opacity: 1;
  visibility: visible;
  transition: transform var(--main-effect), opacity var(--main-effect), visibility var(--main-effect);
  background-color: #fff;
  margin: 0 auto;
  padding: 48px 20px 40px;
  position: absolute;
}

