    padding-right: 12px;
  }
}

.modal-film__backdrop.is-hidden .modal-film__modal {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: scale(.5);
}

.modal-film__button-close {
  width: 30px;
  height: 30px;
  color: #000;
  transition: transform var(--main-effect), color var(--main-effect), border-color var(--main-effect);
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal-film__button-close:hover, .modal-film__button-close:focus {
